:root {
  /* font */
  --main-font: 'Georgia', sans-serif;
  --secondary-font: 'Arial', sans-serif;

  /* colors */
  --main-white-cl: #ffffff;
  --accent-cl: #f1cdb3;

  /* text colors */
  --main-txt-cl: #545454;
  --main-txt-cl-acc: #4c4c4c;
  --title-txt-cl: #ffffff;
  --title-txt-cl-btn: #292929;
  --accent-cl-sign: #b2b2b2;
  --accent-cl-light: #fafafa;
  --accent-cl-dark: #cdcdcd;

  /* background colors */
  --main-bg-cl: #e5e5e5;
  --secondary-bg-cl: #f6f6f6;
  --dark-bg-cl: radial-gradient(100% 215.42% at 0% 0%, #5b483a 0%, #262425 100%),
    #211f20;

  /* Animation */
  --main-animation: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-style: normal;
  font-size: 15px;
  line-height: 160%;
  font-weight: 400;
}

