.wrapper {
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.section {
  padding: 80px 0px 100px 0px;
  margin: 0 auto;
}
.title {
  font-family: var(--main-font);
  font-style: normal;
  font-weight: 400;
  font-size: 44px;
  line-height: 130%;
}
.not-scroll {
  overflow: hidden;
