/*
============================
| Modal form / Модальное окно
============================
*/

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(41, 41, 41, 0.6);
  opacity: 1;
  z-index: 10;
  transition: opacity var(--main-animation), visibility var(--main-animation);
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  width: 900px;
  height: 500px;
  border-radius: 9px;
  background-color: var(--accent-cl-light);
  box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
  transition: opacity var(--main-animation), visibility var(--main-animation);
}

.modal-close-btn {
  position: absolute;
  top: -52px;
  right: -42px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 52px;
  padding: 0;
  border: 2px solid #f1cdb3;
  border-radius: 100%;
  background-color: transparent;
  cursor: pointer;
  transition: background-color var(--main-animation);
}
.modal-close-btn:hover,
.modal-close-btn:focus {
  background-color: #fddcc4;
  border: 0;
}
.modal-close-icon {
  color: var(--title-txt-cl-btn);
}
.modal-main-cont {
  display: flex;
}
.modal-img {
  border-radius: 9px;
}
.modal-cont {
  padding: 50px 20px 0px 30px;
}
.modal-title {
  font-family: var(--main-font);
  color: #000000;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 0.06em;
}
.modal-subtitle {
  margin-top: 10px;
  margin-bottom: 40px;
  font-family: var(--main-font);
  color: #000000;
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
}

.modal-text {
  margin-bottom: 40px;
  padding-left: 1px;
  font-family: var(--main-font);
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
}
.modal-info {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
}
.modal-info .accent-dark {
  font-weight: 700;
}
.modal-info:last-child {
  margin-bottom: 0;
}
.modal-dot-icon {
  margin-right: 10px;
}

