  --title-txt-cl: #fff;
  --title-txt-cl-btn: #292929;
  --accent-cl-sign: #b2b2b2;
  --accent-cl-light: #fafafa;
  --accent-cl-dark: #cdcdcd;
  --main-bg-cl: #e5e5e5;
  --secondary-bg-cl: #f6f6f6;
  --dark-bg-cl: radial-gradient(100% 215.42% at 0% 0%, #5b483a 0%, #262425 100%), #211f20;
