.menu {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.menu__list {
  gap: 35px;
  display: flex;
}

.menu__link {
  color: #cdcdcd;
  font-family: Arial, Helvetica, sans-serif;
}

.menu__link:hover {
  color: #fafafa;
  transition: all .3s;
}

.menu__link._light-bg {
  color: #545454;
}

.menu__link._light-bg:hover {
  color: #292929;
  transition: all .3s;
}

.menu__link._active {
  color: #fafafa;
  cursor: default;
  text-underline-offset: 7px;
  text-decoration: underline #f1cdb3;
  text-decoration-thickness: 3px;
}

.menu__link._light-bg._active {
  color: #292929;
}

.menu__icon {
  display: none;
}

@media (max-width: 767px) {
  .menu__icon {
    z-index: 5;
    width: 30px;
    height: 22px;
    cursor: pointer;
    transition: all .3s;
    display: block;
    position: relative;
    top: 5px;
  }

  .menu__icon > span, .menu__icon:before, .menu__icon:after {
    height: 10%;
    width: 100%;
    background-color: #f1cdb3;
    transition: all .3s;
    position: absolute;
    left: 0;
  }

  .menu__icon:before, .menu__icon:after {
    content: "";
  }

  .menu__icon:before {
    top: 0;
  }

  .menu__icon:after {
    bottom: 0;
  }

  .menu__icon > span {
    top: 50%;
    transform: scale(1)translate(0, -50%);
  }

  .menu__icon._light-bg > span, .menu__icon._light-bg:before, .menu__icon._light-bg:after {
    background-color: #000;
  }

  .menu__icon._active {
    transform: rotate(90deg);
  }

  .menu__body {
    width: 100%;
    height: 100%;
    max-width: 320px;
    background-color: #292929;
    padding: 248px 0 0;
    transition: all .3s;
    position: fixed;
    top: 0;
    right: -100%;
  }

  .menu__body._light-bg {
    background-color: #fff;
  }

  .menu__body._active {
    right: 0;
  }

  .menu__body-wrapper {
    width: 100%;
    height: 100%;
    background: rgba(41, 41, 41, .6);
    transition: all;
    position: fixed;
    top: 0;
    left: -100%;
  }

  .menu__body-wrapper._active {
    left: 0;
  }

  .menu__list {
    flex-direction: column;
    align-items: center;
  }
}

